.timetable__now {
  pointer-events: none;
  position: absolute;
  left: 55px;
  display: -ms-flexbox;
  display: flex; }

.timetable__now-line-segment {
  width: 350px;
  color: rgba(255, 0, 0, 0.1);
  background-color: currentColor;
  height: 2px; }

@media (max-width: 420px) {
  .timetable__now-line-segment {
    width: calc(100vw - 45px - 30px); } }



