.timetable__events-in-location {
  width: 350px;
  padding: 10px; }

.timetable__events-in-location-content {
  position: relative; }

.timetable__location-header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  margin-top: -10px;
  z-index: 1;
  background-color: white; }

@media (max-width: 420px) {
  .timetable__events-in-location {
    width: calc(100vw - 45px - 30px); } }



