.app__header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white; }

.app__title {
  font-size: 1.5em; }

.app__intro {
  font-size: large; }

@-webkit-keyframes app__logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes app__logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }



